import React, { useState, useEffect } from 'react';
import TextArea from './TextArea';
import MoodSlider from './MoodSlider';
import { EMOTIONS_LIST } from '../../utils/constants';
import './JournalEntryForm.css';

const JournalEntryForm = ({
    entry = null,
    onSave,
    onCancel,
    isNew = false,
    isEditing = false
}) => {
    const [content, setContent] = useState('');
    const [mood, setMood] = useState(5);
    const [emotions, setEmotions] = useState([]);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (entry) {
            setContent(entry.content || '');
            setMood(entry.mood || 5);
            setEmotions(entry.emotions || []);
        }
    }, [entry]);

    const handleEmotionToggle = (emotion) => {
        setEmotions(prev => 
            prev.includes(emotion)
                ? prev.filter(e => e !== emotion)
                : [...prev, emotion]
        );
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (!content.trim()) {
            alert('Please write something in your journal entry.');
            return;
        }

        setLoading(true);
        try {
            const entryData = {
                content: content.trim(),
                mood,
                emotions,
                createdAt: entry?.createdAt || new Date().toISOString()
            };

            await onSave(entryData);
        } catch (error) {
            console.error('Error saving entry:', error);
            alert('Failed to save entry. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handleCancel = () => {
        if (content.trim() && !window.confirm('Are you sure you want to cancel? Your changes will be lost.')) {
            return;
        }
        onCancel();
    };

    return (
        <form className="journal-entry-form" onSubmit={handleSubmit}>
            <div className="form-header">
                <h2>{isNew ? 'New Journal Entry' : 'Edit Entry'}</h2>
                <p>{isNew ? 'Capture your thoughts and feelings' : 'Update your entry'}</p>
            </div>

            <div className="form-content">
                {/* Mood Slider */}
                <div className="form-section">
                    <MoodSlider
                        value={mood}
                        onChange={setMood}
                        disabled={loading}
                    />
                </div>

                {/* Text Area */}
                <div className="form-section">
                    <TextArea
                        value={content}
                        onChange={setContent}
                        placeholder="How was your day? What's on your mind? Share your thoughts and feelings..."
                        disabled={loading}
                    />
                </div>

                {/* Emotions */}
                <div className="form-section">
                    <label className="emotions-label">
                        What emotions are you feeling? (Optional)
                    </label>
                    <div className="emotions-grid">
                        {EMOTIONS_LIST.map(emotion => (
                            <button
                                key={emotion}
                                type="button"
                                className={`emotion-chip ${emotions.includes(emotion) ? 'selected' : ''}`}
                                onClick={() => handleEmotionToggle(emotion)}
                                disabled={loading}
                            >
                                {emotion}
                            </button>
                        ))}
                    </div>
                </div>
            </div>

            <div className="form-actions">
                <button
                    type="button"
                    className="btn btn-secondary"
                    onClick={handleCancel}
                    disabled={loading}
                >
                    Cancel
                </button>
                <button
                    type="submit"
                    className="btn btn-primary"
                    disabled={loading || !content.trim()}
                >
                    {loading ? 'Saving...' : (isNew ? 'Save Entry' : 'Update Entry')}
                </button>
            </div>
        </form>
    );
};

export default JournalEntryForm;
